@import "config/themes/_diamonds";

$gray: #94939f;

@mixin fillDiamondBorder($color) {
  & > g > path:last-child {
    fill: $color;
  }
}

@mixin fillDiamond($color) {
  fill: $color;
}

.withBorderColorless {
  @include fillDiamondBorder($gray);
}

.withoutBorderColorless {
  @include fillDiamond(#201e2f);
}

.withoutBorderGray {
  @include fillDiamond($gray);
}

.withBorderRed {
  @include fillDiamondBorder($redDiamondColor);
}

.withoutBorderRed {
  @include fillDiamond($redDiamondColor);
}

.withBorderAzure {
  @include fillDiamondBorder($azureDiamondColor);
}

.withoutBorderAzure {
  @include fillDiamond($azureDiamondColor);
}

.withBorderPink {
  @include fillDiamondBorder($pinkDiamondColor);
}

.withoutBorderPink {
  @include fillDiamond($pinkDiamondColor);
}

.withBorderBlue {
  @include fillDiamondBorder($blueDiamondColor);
}

.withoutBorderBlue {
  @include fillDiamond($blueDiamondColor);
}

.withBorderGreen {
  @include fillDiamondBorder($greenDiamondColor);
}

.withoutBorderGreen {
  @include fillDiamond($greenDiamondColor);
}

.withBorderPurple {
  @include fillDiamondBorder($purpleDiamondColor);
}

.withoutBorderPurple {
  @include fillDiamond($purpleDiamondColor);
}

.withBorderYellow {
  @include fillDiamondBorder($yellowDiamondColor);
}

.withoutBorderYellow {
  @include fillDiamond($yellowDiamondColor);
}
