@import "sass/index";

.root {
  display: flex;
  width: 100%;
  background-color: $martinique;
  border-radius: 6px;
  padding: 20px;
  &::before {
    position: absolute;
    content: "";
    width: 0;
    height: 0;
    border-width: 4px 6px 4px 0px;
    border-color: $martinique;
    border-style: solid;
    transform: translateX(-22px) rotate(45deg);
    top: 45%;
  }
  & > div:last-child {
    margin-left: 20px;
  }
}
