@import "sass/index";
@import "config/themes/_diamonds";

$font: "Varela Round";

.root {
  width: 100%;
}

.field {
  display: flex;
  min-width: none;
  height: 60px;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.05);
  border: 1px solid #464560;
  border-radius: 8px;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 10px;
  padding-right: 10px;
}

.coin {
  position: static;
  width: 28px;
  height: 28px;
}

.amount {
  font-family: $font;
  font-size: 18px;
  line-height: 22px;
  color: white;
}

.text {
  font-family: $font;
  font-size: 16px;
  line-height: 19px;
  color: white;
}

.label {
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 14px;
  color: rgba($white, 0.8);
}
