@import "sass/index";

$borderRadius: 6px;
$indent: 10px;

.root {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $martinique;
  border-radius: $borderRadius;
  padding: $indent;
  transition: background-color 0.1s;
  cursor: help;
  &:first-child {
    & .infoWrapper {
      top: 0;
    }
    & .infoWrapper > div::before {
      top: 15%;
    }
  }
  &:last-child {
    & .infoWrapper {
      bottom: 0;
    }
    & .infoWrapper > div::before {
      top: 75%;
    }
  }
}

.selected {
  background-color: $gunPowder;
}

.combination {
  display: inline-block;
  height: 20px;
  & > svg {
    margin-right: $indent;
  }
}

.ratio {
  font-family: "Varela Round";
  font-size: 18px;
  color: $white;
  user-select: none;
}

.infoWrapper {
  display: none;
  width: 145%;
  position: absolute;
  left: calc(100% + 17px);
  &:hover {
    display: block;
    cursor: default;
  }
}

.showInfo {
  display: block;
  @media (max-width: 1090px) {
    display: none;
  }
}
