$fieldWidth: 1086px;
$fieldHeight: 220px;

.root {
  display: flex;
  justify-content: center;
  align-items: center;
}

.field {
  height: $fieldHeight;
  display: flex;
  flex-shrink: 0;
  justify-content: space-around;
  align-items: flex-end;
  width: 100%;
  @media (max-width: 1520px) {
    width: var(--fieldWidth);
    justify-content: space-between;
  }
}
